@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;800&display=swap');

body {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding: 50px;
    background: lightgray;

}

/**************main*************/
.added{
  border: none;
  box-shadow: none;
  padding: 0;
}
.main{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 50px;
  background: white;
  /* display: flex;
  justify-content: space-between; */
  padding: 40px 80px;
  padding-top: 70px;
  margin: 0 auto;
  width: 695px;
  height: 640px;
  position: relative;

  
}
/**************calculator*************/
.calculator{
  width: 235px;
  height: 450px;
  font-weight: 500;
  font-size: 14px;
  padding-top: 0;
  justify-self: left;
  /* grid-area: 2 / 1 / 3 / 2; */


  

}
.btn{
  display: block;
  width: 100%;
  border: 1px solid #E2E3E5;
  padding: 12px;

}
.btn:hover{
  background: #FFFFFF;
  outline: 2px solid #5D5FEF;
  color: #000000;
}
.btn:active{
  background: #5D5FEF;
  color: #FFFFFF;

}
.btn:focus{
  background: #5D5FEF;
  color: #FFFFFF;
  outline: none;
  box-shadow: none;
}
/**************display*************/
.display{
  border: 1px solid #E2E3E5;
  border-radius: 6px;
  padding: 5px;
  cursor: move;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 5px;
}
.display__btn{
  background: #F3F4F6;
  font-weight: 800;
  font-size: 36px;
  padding: 5px;
  text-align: right;

}
/**************equals-sign*************/
.equals-sign{
  border: 1px solid #E2E3E5;
  border-radius: 6px;
  padding: 5px;
  cursor: move;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
}
.equals-sign__btn{
  background-color: #5D5FEF;
  color: white;
}
/**************numbers*************/
.numbers{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  border: 1px solid #E2E3E5;
  border-radius: 6px;
  padding: 5px;
  cursor: move;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 5px;
}

.div1 { grid-area: 3 / 1 / 4 / 2; }
.div2 { grid-area: 3 / 2 / 4 / 3; }
.div3 { grid-area: 3 / 3 / 4 / 4; }
.div4 { grid-area: 2 / 1 / 3 / 2; }
.div5 { grid-area: 2 / 2 / 3 / 3; }
.div6 { grid-area: 2 / 3 / 3 / 4; }
.div7 { grid-area: 1 / 1 / 2 / 2; }
.div8 { grid-area: 1 / 2 / 2 / 3; }
.div9 { grid-area: 1 / 3 / 2 / 4; }
.div10 { grid-area: 4 / 1 / 5 / 3; }
.div11 { grid-area: 4 / 3 / 5 / 4; }

.zero {
   grid-area: 4 / 1 / 5 / 3;
}

.operation-buttons{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 10px;
  border: 1px solid #E2E3E5;
  border-radius: 6px;
  padding: 5px;
  cursor: move;
  -webkit-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.06), 0px 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 5px;
}

/**************dnd*************/
.dnd{
  position: absolute;
  top: 30px;
  left: 380px;
  height: 97%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.5fr 3fr;
  grid-row-gap: 5px;
  width: 40%;
  justify-self: right;
  pointer-events: none; /**запрещаем перетаскивание*****/
  /* grid-area: 2 / 2 / 3 / 3; */
  /* grid-row: 2;
  grid-column: 2; */

}
.dnd__frame{

  border: 2px dashed #C4C4C4;
  border-radius: 6px;
  height: 448px;
  width: 235px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: -25px;


}
.dnd__frame__icon{
  margin-bottom: 10px;
}
.dnd__frame__subtitle{
  color: #5D5FEF;
  margin-bottom: 5px;
}
.dnd__frame__text{
  color: #6B7280;
  font-size: 12px;
}

/**************runtimeToggle*************/
.runtime-toggle{
  display: flex;
  width: 235px;
  max-height: 40px;
  pointer-events: auto;
  /* grid-area: 1 / 2 / 2 / 3; */

}
/**************runtime*************/
.runtime{
  width: 50%;
  height: 36px;
}
.runtime__btn{
  font-size: 14px;
  height: 40px;
  color: #4D5562;
  background: #F3F4F6;
  line-height: 0.5rem;
}
.runtime__btn:hover{
  background: #FFFFFF;
  border: 2px solid #5D5FEF;
}
.runtime__btn:active{
  background: #FFFFFF;
  border: 1px solid #E2E3E5;
  border-radius: 5px;
  color: #4D5562;

}
.runtime__btn:focus{
  background: #FFFFFF;
  border: 1px solid #E2E3E5;
  border-radius: 5px;
  color: #4D5562;
}
.icon{
  margin-right: 7px;
  margin-left: -10px;
  width: 12px;
}
.icon:hover{
  color:  #5D5FEF;
}
.icon:active{
  color:  #5D5FEF;
}
.runtime-main{
 display: flex;
 flex-direction: column;
 align-items: flex-end;
 padding-top: 30px;
 padding-right: 70px;
}
.runtime-calculator{
  margin-top: 30px;
}
.runtime-calculator div{
  box-shadow: none;
  border: none;
}
/**************constructor*************/
.constructor{
  width: 50%;
  height: 36px;
}
.constructor__btn{
  font-size: 14px;
  height: 40px;
  color: #4D5562;
  background: #F3F4F6;
  line-height: 0.5rem;
}
.constructor__btn:hover{
  background: #FFFFFF;
  border: 2px solid #5D5FEF;
  box-shadow: none;
}
.constructor__btn:active{
  background: #FFFFFF;
  border: 1px solid #E2E3E5;
  border-radius: 5px;
  color: #4D5562;

}
.constructor__btn:focus{
  background: #FFFFFF;
  border: 1px solid #E2E3E5;
  border-radius: 5px;
  color: #4D5562;
}

